/* harmony default export */ var chat_body = ({
  components: {
    "message-list": src_components_message_list,
    "user-list": src_components_user_list
  },
  data: function data() {
    return {
      msgList: [],
      usgList: [],
      ws: ''
    };
  },
  created: function created() {
    var ws = new WebSocket("ws://" + location.hostname + ":3000/ws/chat");
    this.ws = ws;
    function addToUserList(usgList, user) {
      for (var i = 0, length = usgList.length; i < length; i++) {
        if (usgList[i].id === user.id) {
          return;
        }
      }
      usgList.push(user);
    }
    function addMessage(msgList, msg) {
      msgList.push(msg);
    }
    function removeFromUserList(usgList, user) {
      var target = -1;
      for (var i = 0, length = usgList.length; i < length; i++) {
        if (usgList[i].id === user.id) {
          target = i;
          break;
        }
      }
      if (target >= 0) {
        usgList.splice(target, 1);
      }
    }
    var that = this;
    ws.onmessage = function (event) {
      var data = event.data;
      console.log(data);
      var msg = JSON.parse(data);
      if (msg.type === "list") {
        //that.usgList = msg.data;
      } else if (msg.type === "join") {

        addToUserList(that.usgList, msg.user);
        addMessage(that.msgList, msg);
      } else if (msg.type === "left") {
        removeFromUserList(that.usgList, msg.user);
        addMessage(that.msgList, msg);
      } else if (msg.type === "chat") {
        addMessage(that.msgList, msg);
      }
    };
  },
  methods: {
    send: function send(value) {
      console.log(value);
      this.ws.send(value.trim());
    }
  }
});
